@import 'normalize';
@import 'typography';
@import 'navigation';
@import 'sidebar';
@import 'comments';
@import 'footer';
/*
====================================
Default Styling
====================================
*/

a:active, a:hover {
  outline: 0; }

img {
	max-width: 100%;
}

body {
	color: #333;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

.content {
	float: right;
	width: 85%;
}

.full {
	width: 100%;
}

.header {
	height: 100vh;
}

.backgroundImage {
	height: 100%;
	z-index: 1;
}

.buffer {
	text-align: center;
	padding-top: 30vh;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
	h1 {
		margin: 0;
		font-size: 60px;
	}
}

.buffer h2 {
	margin: 0;
}

button.what {
	background: $green;
	color: white;
	font-family: 'Open Sans', sans-serif;
	box-shadow:inset 0px 1px 0px 0px #7a918d;
	background-color:#7a918d;
	border-radius:1px;
	border:1px solid #7a918d;
	display:inline-block;
	cursor:pointer;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
	margin-top: 2%;
}
button.what:hover {
	background-color:#7a918d;
}
button.what:active {
	position:relative;
	top:1px;
}

.bio {
	display: flex;
	background: $grey;
	width: 100%;
}

img.headshot {
	height: 50%;
	width: 50%;
}

.biotext {
	margin: 30px;
	button {
		margin-top: 25px;
		background: none;
		border: 2px solid $purple;
		padding: 10px;
	}
}

.logo {
	width: 70%;
	height: 70%;
	margin-bottom: 25%;
	margin-top: 20%;
	margin-left: 20%;
}

.sidebarContainer {
	height: 100%;
	width: 15%;
	position: fixed;
	top: 0;
	left: 0;
	// background: white;
}

.getMain {
	width: 85%;
}

.menuContainer {
	margin-left: 27%;
}

// to get content on the right 
.sidebarWrap {
	display: flex;
	justify-content: flex-end; 
}

.bio.content {
	margin-bottom: 50px;
}

//portfolio section
.portfolio {
	background: $purple;
	padding: 30px;
	h4 {
	color: white;
	&:after {
		width: 25%;
	}
}
}

.portfolio p {
	color: white;
}

//expert section

section.expert {
	text-align: center;
	background: $grey;
	padding-top: 50px;
	padding-left: 50px;
	h4 {
	color: black;
	margin-top: 0;
	&:after {
		margin: 0 auto;
		width: 30%;
	}
}
}

.expertise {
	width: 26%;
	display: flex;
	strong {
		font-size: 25px;
	}
}

.expertIcon {
	border: 10px solid $green;
	border-radius: 50%;
}

